import { Modal } from 'antd';
import * as api from '../util/api';
import React from 'react';
import moment from 'moment';

export default class ConfirmModal extends React.Component {
  render() {
    const {
      handleConfirmModalClose,
      visible,
      confirmDropAppointment,
      confirmDropDoctorDayOff,
      confirmTitle,
      droppedEventType,
      actionType,
      deleteAppointmentId,
      deleteDoctorDayOffId,
    } = this.props;

    let title;
    if (actionType === 'editDate') {
      title = '';
    } else if (actionType === 'deleteEvent') {
      title = '';
    }

    let confirmModalContent;
    if (actionType === 'editDate') {
      if (droppedEventType === 'appointment') {
        confirmModalContent = (
          <p>
            {confirmTitle} {' '}
            {moment(confirmDropAppointment.expectedArrivalTime).format('LLLL')}
          </p>
        );
      } else if (droppedEventType === 'doctorDayOff') {
        confirmModalContent = (
          <p>
            {confirmTitle} {' '}
            {moment(confirmDropDoctorDayOff.start).format('LLLL')}
          </p>
        );
      } else if (droppedEventType === 'clinicDayOff') {
        confirmModalContent = (
          <p>
            {confirmTitle} {' '}
            {moment(confirmDropDoctorDayOff.start).format('LLLL')}
          </p>
        );
      }
    } else if (actionType === 'deleteEvent') {
      if (droppedEventType === 'appointment') {
        confirmModalContent = (
          <p>
            {confirmTitle}
            <br />
            
            <br />
            {moment(confirmDropAppointment.expectedArrivalTime).format('LLLL')}
            
          </p>
        );
      } else if (droppedEventType === 'doctorDayOff') {
        confirmModalContent = (
          <p>
            {confirmTitle}
            <br /> {moment(confirmDropDoctorDayOff.start).format('LLLL')}{' '}
            
          </p>
        );
      } else if (droppedEventType === 'clinicDayOff') {
        confirmModalContent = (
          <p>
            {confirmTitle}
            <br /> {moment(confirmDropDoctorDayOff.start).format('LLLL')}
          </p>
        );
      }
    }
    return (
      <Modal
        centered
        title={title}
        visible={visible}
        okText=""
        cancelText=""
        onOk={async () => {
          if (actionType === 'editDate') {
            if (droppedEventType === 'appointment') {
              await api.editAppointment(confirmDropAppointment);
            } else if (
              ['doctorDayOff', 'clinicDayOff'].includes(droppedEventType)
            ) {
              await api.editDoctorDayOff(confirmDropDoctorDayOff);
            }
          } else if (actionType === 'deleteEvent') {
            if (droppedEventType === 'appointment') {
              await api.deleteAppointment(deleteAppointmentId);
            } else if (
              ['doctorDayOff', 'clinicDayOff'].includes(droppedEventType)
            ) {
              await api.deleteDoctorDayOff(deleteDoctorDayOffId);
            }
          }
          handleConfirmModalClose(false);
        }}
        onCancel={() => {
          handleConfirmModalClose(false);
        }}
      >
        {confirmModalContent}
      </Modal>
    );
  }
}
