import React from 'react';
import { render } from 'react-dom';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import listPlugin from '@fullcalendar/list';
import resourceTimeGridPlugin from '@fullcalendar/resource-timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import zhTW from '@fullcalendar/core/locales/zh-tw';
import {
  Icon,
  Popover,
  Select,
  Button,
  Calendar,
  Popconfirm,
  Menu,
  Dropdown,
  Row,
  Col,
  Typography,
  message,
  Checkbox,
} from 'antd';
import moment from 'moment';
import * as api from '../util/api';
import * as log from 'loglevel';
import AppointmentModal from './AppointmentModal';
import ConfirmModal from './ConfirmModal';
import './AppointmentScheduler.css';
import 'moment/locale/zh-tw';
import * as helpers from '../util/helpers';

const { Title } = Typography;

export const emptyAppointment = {
  note: '',
  microscope: false,
  baseFloor: false,
  requiredTreatmentTime: 15,
  doctor: { id: undefined },
  patient: { id: undefined, newPatient: true },
  expectedArrivalTime: moment(),
};

export const emptyDoctorDayOff = {
  start: moment()
    .hour(8)
    .minute(0),
  end: moment()
    .hour(22)
    .minute(0),
  timeType: 'WORK_TIME',
  note: '',
};

export const emptyNewPatient = {
  birth: moment().format('YYYY-MM-DD'),
  name: '',
  phone: '',
  gender: 'OTHER',
};

export default class AppointmentScheduler extends React.Component {
  calendarComponentRef = React.createRef();
  clicks = 0;
  state = {
    start: moment(),
    end: moment(),
    calendarEvents: [{ title: 'Event Now', start: new Date() }],
    doctors: [],
    displayDoctors: [],
    visible: false,
    confirmModalVisible: false,
    confirmDropAppointment: {},
    confirmDropDoctorDayOff: {},
    modalMode: '',
    confirmLoading: false,
    confirmTitle: '',
    appointment: emptyAppointment,
    additionalData: {},
    doctorDayOffObject: emptyDoctorDayOff,
    selectedDoctors: 'firstRender',
    checkBoxDoctorsList: [],
    checkBoxDoctorsListAll: [],
    droppedEventType: '',
    createNewPatient: true,
    newPatient: emptyNewPatient,
    actionType: '',
    deleteAppointmentId: 0,
    deleteDoctorDayOffId: 0,
    editEventMode: false,
    navigationCalendarValue: moment(),
    currentViewType: '',
    selectAllDoctorsBool: false,
  };

  async componentDidMount() {
    const { Option } = Select;
    const response = await api.getDoctors();
    if (response && response.data) {
      const doctors = response.data
        .filter(user => {
          return user.authorities.includes('ROLE_DOCTOR');
        })
        .map(doctor => {
          doctor.value = doctor.login;
          doctor.text = doctor.firstName;
          return doctor;
        });
      const displayDoctors = doctors
        .filter(doctor => doctor.activated)
        .map(doctor => {
          return <Option key={doctor.id}>{doctor.firstName}</Option>;
        });

      let checkBoxDoctorsListAll = [];
      doctors
        .filter(doctor => doctor.activated)
        .forEach(doctor => {
          checkBoxDoctorsListAll.push(doctor.id);
        });

      log.debug(response.data, doctors, displayDoctors);
      this.setState(
        {
          doctors,
          displayDoctors,
          checkBoxDoctorsList: [checkBoxDoctorsListAll[0]],
          selectedDoctors: checkBoxDoctorsListAll[0].toString(),
          checkBoxDoctorsListAll,
        },
        () => {
          const { start, end, selectedDoctors } = this.state;
          this.props.fetchAppointments(start, end, selectedDoctors);
        },
      );
    }
  }

  // schedule control

  selectAllDoctorsClick = async b => {
    const selectAllDoctorsBool = b.target.checked;
    const { checkBoxDoctorsListAll } = this.state;
    let selectedDoctor = [];
    if (selectAllDoctorsBool) {
      await this.setState({ selectAllDoctorsBool });
      selectedDoctor = checkBoxDoctorsListAll;
    } else {
      await this.setState({ selectAllDoctorsBool });
    }

    this.handleDoctorChange(selectedDoctor);
  };

  handleDoctorChange = d => {
    const { start, end, checkBoxDoctorsListAll } = this.state;
    const selectAllDoctorsBool = checkBoxDoctorsListAll.length === d.length;
    const doctors = d.length ? d.toString() : 'doNotQuery';
    this.setState(
      {
        selectedDoctors: doctors,
        checkBoxDoctorsList: d,
        selectAllDoctorsBool,
      },
      () => {
        this.props.fetchAppointments(start, end, doctors);
      },
    );
  };

  resetAppointment = () => {
    this.setState({
      confirmLoading: false,
      visible: false,
      confirmModalVisible: false,
      appointment: emptyAppointment,
      additionalData: {},
      modalMode: '',
      editEventMode: false,
      doctorDayOffObject: emptyDoctorDayOff,
    });
  };

  columnHeaderHtml = date => {
    const momentDate = moment(date);
    return `<div class="columnHeaderHtml"><div><p>${momentDate.format(
      'M/D',
    )}</p><p>${momentDate.format('ddd')}</p></div></div>`;
  };

  handleEventResize = async info => {
    log.debug(info);
    if (info.event.extendedProps.eventType === 'appointment') {
      const requiredTreatmentTime = moment(info.event.end).diff(
        moment(info.event.start),
        'minutes',
      );
      const appointment = {
        ...info.event.extendedProps.appointment,
        requiredTreatmentTime,
      };
      await api.editAppointment(appointment);
    } else if (
      ['doctorDayOff', 'clinicDayOff'].includes(
        info.event.extendedProps.eventType,
      )
    ) {
      const end = moment(info.event.end).toISOString();
      const doctorDayOff = {
        ...info.event.extendedProps.doctorDayOffObject,
        end,
      };
      await api.editDoctorDayOff(doctorDayOff);
    }
    const { start, end, selectedDoctors } = this.state;
    this.props.fetchAppointments(start, end, selectedDoctors);
  };

  handleEventMouseEnter = info => {
    log.debug(info.event.title + ' was mouse entered');
  };

  handleEventMouseLeave = info => {
    log.debug(info.event.title + ' was mouse left');
  };

  handleDayAndViewRender = async info => {
    log.debug(info);
    const start = moment(info.view.activeStart);
    const end = moment(info.view.activeEnd);
    const { selectedDoctors, navigationCalendarValue } = this.state;
    const currentViewType = info.view.type;
    if (
      moment(navigationCalendarValue).isBefore(start) ||
      end.isBefore(moment(navigationCalendarValue))
    ) {
      this.setState({ navigationCalendarValue: start });
    }

    if (selectedDoctors === 'firstRender') {
      this.setState({ start, end, currentViewType });
      return;
    }
    this.setState({ start, end, currentViewType }, () => {
      this.props.fetchAppointments(start, end, selectedDoctors);
    });

    const { cookies } = this.props;
    cookies.set('defaultView', info.view.type);
  };

  handleDateSelect = date => {
    let calendarApi = this.calendarComponentRef.current.getApi();
    this.setState({ navigationCalendarValue: moment(date) }, () => {
      calendarApi.gotoDate(date.format('YYYY-MM-DD'));
    });
  };

  handleCancelModal = () => {
    this.resetAppointment();
  };

  showModal = arg => {
    this.setState({
      visible: true,
      modalMode: arg.modalMode,
      appointment: arg.appointment,
      doctorDayOffObject: arg.doctorDayOffObject,
      createNewPatient: arg.createNewPatient,
      newPatient: arg.newPatient,
      editEventMode: arg.editEventMode,
    });
  };

  handleDateClick = arg => {
    log.debug(arg);
    this.clicks++;
    if (this.clicks === 1) {
      setTimeout(() => {
        if (this.clicks > 1) {
          // Double click
          this.handleDbClickDate(arg);
        }
        this.clicks = 0;
      }, 400);
    }
  };

  handleDbClickDate = arg => {
    const date = moment(arg.date).toISOString();
    const appointment = {
      ...emptyAppointment,
      expectedArrivalTime: date,
    };
    const doctorDayOffObject = {
      ...emptyDoctorDayOff,
      start: moment(date).startOf('day'),
      end: moment(date).endOf('day'),
    };
    this.showModal({
      appointment,
      modalMode: 'appointment',
      doctorDayOffObject,
      createNewPatient: true,
      newPatient: emptyNewPatient,
      editEventMode: false,
    });
  };

  handleNewAppointment = () => {
    this.handleDbClickDate({ date: moment() });
  };

  menuClick = e => {
    if (e.key === 'editAppointment') {
      const { appointment, additionaldata: additionalData } = e.item.props;
      this.setState({
        appointment,
        additionalData,
        visible: true,
        modalMode: 'appointment',
        createNewPatient: false,
        editEventMode: true,
      });
    } else if (e.key === 'deleteAppointment') {
      const id = e.item.props.id;
      const confirmTitle = e.item.props.title;
      const confirmDropAppointment = { expectedArrivalTime: e.item.props.date };
      this.setState({
        confirmModalVisible: true,
        deleteAppointmentId: id,
        droppedEventType: 'appointment',
        actionType: 'deleteEvent',
        confirmTitle,
        confirmDropAppointment,
      });
    } else if (e.key === 'deleteDoctorDayOff') {
      const id = e.item.props.id;
      const confirmTitle = e.item.props.title;
      const confirmDropDoctorDayOff = { start: e.item.props.date };
      this.setState({
        confirmModalVisible: true,
        deleteDoctorDayOffId: id,
        droppedEventType: 'doctorDayOff',
        actionType: 'deleteEvent',
        confirmTitle,
        confirmDropDoctorDayOff,
      });
    } else if (e.key === 'editDoctorDayOff') {
      const doctorDayOffObject = e.item.props.doctordayoffobject;
      log.debug(doctorDayOffObject);
      this.setState({
        doctorDayOffObject,
        visible: true,
        modalMode: 'doctorDayOff',
        editEventMode: true,
        createNewPatient: false,
      });
    } else if (e.key === 'deleteClinicDayOff') {
      const id = e.item.props.id;
      const confirmTitle = e.item.props.title;
      const confirmDropDoctorDayOff = { start: e.item.props.date };
      this.setState({
        confirmModalVisible: true,
        deleteDoctorDayOffId: id,
        droppedEventType: 'clinicDayOff',
        actionType: 'deleteEvent',
        confirmTitle,
        confirmDropDoctorDayOff,
      });
    } else if (e.key === 'editClinicDayOff') {
      const doctorDayOffObject = e.item.props.doctordayoffobject;
      log.debug(doctorDayOffObject);
      this.setState({
        doctorDayOffObject,
        visible: true,
        modalMode: 'clinicDayOff',
        editEventMode: true,
        createNewPatient: false,
      });
    }
  };

  confirm = async e => {
    const id = this.state.appointment.id;
    await api.deleteAppointment(id);
    const { start, end, selectedDoctors } = this.state;
    this.props.fetchAppointments(start, end, selectedDoctors);
    message.success('Click on Yes');
  };

  handleEventRender = info => {
    if (info.view.type.indexOf('Grid') !== -1) {
      let content;
      let menu;
      if (info.event.extendedProps.eventType === 'appointment') {
        content = (
          <div style={{ minWidth: '150px' }}>
            <Row>
              <b>
                {`
                [NP] ${info.event.extendedProps.additionalData.name} ${
                  info.event.extendedProps.additionalData.age
                    ? info.event.extendedProps.additionalData.age + ''
                    : ''
                }
              `}
              </b>
            </Row>
            <Row>
              {`
                ${
                  info.event.extendedProps.additionalData.birth
                    ? info.event.extendedProps.additionalData.birth + ', '
                    : ''
                }
                ${helpers.translateGender(
                  info.event.extendedProps.additionalData.gender,
                )}
              `}
            </Row>
            <Row>
              <Icon type="phone" />{' '}
              {info.event.extendedProps.additionalData.phone}
            </Row>
            <Row>
              <Icon type="user" />{' '}
              {info.event.extendedProps.additionalData.doctor}
            </Row>
            <Row>
              <Icon type="file-text" />{' '}
              {info.event.extendedProps.appointment.note}
            </Row>
            <Row>
              {info.event.extendedProps.requiredTreatmentTime
                ? ''(info.event.extendedProps.requiredTreatmentTime)
                : null}
            </Row>
            <Row type="flex" justify="end">
              <Popconfirm
                title="Are you sure delete this task?"
                onConfirm={this.confirm}
                okText="Yes"
                cancelText="No"
              >
                <Button type="link" style={{ padding: 0, color: 'red' }}>
                  <Icon type="delete" />
                </Button>
              </Popconfirm>
            </Row>
          </div>
        );
        menu = (
          <Menu onClick={this.menuClick}>
            <Menu.Item
              key="editAppointment"
              appointment={info.event.extendedProps.appointment}
              additionaldata={info.event.extendedProps.additionalData}
            >
              
            </Menu.Item>
            <Menu.Item
              key="deleteAppointment"
              id={info.event.extendedProps.appointment.id}
              title={info.event.extendedProps.additionalData.name}
              date={info.event.extendedProps.appointment.expectedArrivalTime}
            >
              
            </Menu.Item>
          </Menu>
        );
      } else if (info.event.extendedProps.eventType === 'doctorDayOff') {
        menu = (
          <Menu onClick={this.menuClick}>
            <Menu.Item
              key="editDoctorDayOff"
              doctordayoffobject={info.event.extendedProps.doctorDayOffObject}
            >
              
            </Menu.Item>
            <Menu.Item
              key="deleteDoctorDayOff"
              id={info.event.extendedProps.doctorDayOffObject.id}
              title={info.event.title}
              date={info.event.extendedProps.doctorDayOffObject.start}
            >
              
            </Menu.Item>
          </Menu>
        );
      } else if (info.event.extendedProps.eventType === 'clinicDayOff') {
        menu = (
          <Menu onClick={this.menuClick}>
            <Menu.Item
              key="editClinicDayOff"
              doctordayoffobject={info.event.extendedProps.doctorDayOffObject}
            >
              
            </Menu.Item>
            <Menu.Item
              key="deleteClinicDayOff"
              id={info.event.extendedProps.doctorDayOffObject.id}
              title={info.event.title}
              date={info.event.extendedProps.doctorDayOffObject.start}
            >
              
            </Menu.Item>
          </Menu>
        );
      }
      if (info.view.type !== 'dayGridMonth') {
        if (info.event.extendedProps.eventType === 'appointment') {
          if (info.el.querySelector('.fc-title')) {
            info.el.querySelector('.fc-title').innerHTML += info.event
              .extendedProps.appointment.note
              ? `<br/> ${info.event.extendedProps.appointment.note}`
              : '';
          }
        } else if (
          ['doctorDayOff', 'clinicDayOff'].includes(
            info.event.extendedProps.eventType,
          )
        ) {
          log.debug(info);
          if (info.el.querySelector('.fc-title')) {
            info.el.querySelector('.fc-title').innerHTML += info.event
              .extendedProps.doctorDayOffObject.note
              ? `<br/> ${info.event.extendedProps.doctorDayOffObject.note}`
              : '';
          }
        }
      }

      if (info.event.extendedProps.eventType === 'appointment') {
        render(
          <Popover content={content} trigger="click" placement="right">
            <Dropdown overlay={menu} trigger={['contextMenu']}>
              <div
                style={{ height: '100%' }}
                dangerouslySetInnerHTML={{ __html: info.el.innerHTML }}
              />
            </Dropdown>
          </Popover>,
          info.el,
        );
      } else {
        render(
          <Dropdown overlay={menu} trigger={['contextMenu']}>
            <div
              style={{ height: '100%' }}
              dangerouslySetInnerHTML={{ __html: info.el.innerHTML }}
            />
          </Dropdown>,
          info.el,
        );
      }
      return info.el;
    } else if (info.view.type === 'listWeek') {
      if (info.event.extendedProps.eventType !== 'appointment') {
        return;
      }

      const regex = /<a>(.*?)<\/a>/;
      const { name, doctor } = info.event.extendedProps.additionalData;
      let { note } = info.event.extendedProps.appointment;
      note = note.length ? ` , ${note}` : '';

      const HTMLContent = info.el.innerHTML.replace(
        regex,
        `<a>${name} , ${doctor}${note}</a>`,
      );
      info.el.innerHTML = HTMLContent;
    }
  };

  handleEventClick = info => {
    log.debug(info.event.title + ' was clicked');
    log.debug(info);
    if (info.event.extendedProps.eventType === 'appointment') {
      this.setState({ appointment: info.event.extendedProps.appointment });
    }
    info.el.addEventListener('dblclick', () => {
      if (info.event.extendedProps.eventType === 'appointment') {
        this.setState({
          appointment: info.event.extendedProps.appointment,
          additionalData: info.event.extendedProps.additionalData,
          visible: true,
          modalMode: 'appointment',
          createNewPatient: false,
          editEventMode: true,
        });
      } else if (info.event.extendedProps.eventType === 'doctorDayOff') {
        log.debug(info.event);
        this.setState({
          doctorDayOffObject: info.event.extendedProps.doctorDayOffObject,
          visible: true,
          modalMode: 'doctorDayOff',
          editEventMode: true,
          createNewPatient: false,
        });
      } else if (info.event.extendedProps.eventType === 'clinicDayOff') {
        log.debug(info.event);
        this.setState({
          doctorDayOffObject: info.event.extendedProps.doctorDayOffObject,
          visible: true,
          modalMode: 'clinicDayOff',
          editEventMode: true,
          createNewPatient: false,
        });
      }
    });
  };

  handleEventDrop = async info => {
    log.debug(
      info.event.title + ' was dropped on ' + info.event.start.toISOString(),
    );
    if (info.event.extendedProps.eventType === 'appointment') {
      const expectedArrivalTime = info.event.start.toISOString();
      const appointment = {
        ...info.event.extendedProps.appointment,
        expectedArrivalTime,
      };
      const confirmTitle = info.event.title;
      setTimeout(() => {
        this.setState({
          confirmModalVisible: true,
          confirmDropAppointment: appointment,
          droppedEventType: 'appointment',
          actionType: 'editDate',
          confirmTitle,
        });
      }, 100);
    } else if (
      ['doctorDayOff', 'clinicDayOff'].includes(
        info.event.extendedProps.eventType,
      )
    ) {
      const start = info.event.start.toISOString();
      const end = info.event.end.toISOString();
      const confirmTitle = info.event.title;
      const doctorDayOff = {
        ...info.event.extendedProps.doctorDayOffObject,
        start,
        end,
      };
      setTimeout(() => {
        this.setState({
          confirmModalVisible: true,
          confirmDropDoctorDayOff: doctorDayOff,
          droppedEventType: info.event.extendedProps.eventType,
          actionType: 'editDate',
          confirmTitle,
        });
      }, 100);
    }
  };

  handleConfirmModalClose = visible => {
    this.setState({ confirmModalVisible: visible });
    const { start, end, selectedDoctors } = this.state;
    this.props.fetchAppointments(start, end, selectedDoctors);
  };

  nextClick = async e => {
    log.debug(e);
    let calendarApi = this.calendarComponentRef.current.getApi();
    await this.syncNavigationCalendar(true);
    calendarApi.next();
  };

  prevClick = async e => {
    log.debug(e);
    let calendarApi = this.calendarComponentRef.current.getApi();
    await this.syncNavigationCalendar(false);
    calendarApi.prev();
  };

  nextYearClick = async e => {
    log.debug(e);
    let calendarApi = this.calendarComponentRef.current.getApi();
    await this.syncNavigationCalendar(true, true);
    calendarApi.nextYear();
  };

  prevYearClick = async e => {
    log.debug(e);
    let calendarApi = this.calendarComponentRef.current.getApi();
    await this.syncNavigationCalendar(false, true);
    calendarApi.prevYear();
  };

  todayClick = e => {
    let calendarApi = this.calendarComponentRef.current.getApi();
    calendarApi.today();
    this.setState({
      navigationCalendarValue: moment(),
    });
  };

  syncNavigationCalendar = async (isForward, isYear) => {
    const constant = isForward ? 1 : -1;
    const { navigationCalendarValue, currentViewType } = this.state;
    let newNavigationCalendarValue;

    if (isYear) {
      newNavigationCalendarValue = moment(navigationCalendarValue).add(
        constant,
        'years',
      );
    } else {
      if (currentViewType === 'resourceTimeGridDay') {
        newNavigationCalendarValue = moment(navigationCalendarValue).add(
          constant,
          'days',
        );
      } else if (currentViewType === 'timeGridWeek') {
        newNavigationCalendarValue = moment(navigationCalendarValue).add(
          constant,
          'weeks',
        );
      } else if (currentViewType === 'dayGridMonth') {
        newNavigationCalendarValue = moment(navigationCalendarValue).add(
          constant,
          'month',
        );
      } else if (currentViewType === 'listWeek') {
        newNavigationCalendarValue = moment(navigationCalendarValue).add(
          constant,
          'weeks',
        );
      }
    }
    await this.setState({
      navigationCalendarValue: newNavigationCalendarValue,
    });
  };

  // modal control
  handleSelectPatient = data => {
    const patient = data.patient;
    this.setState(prevState => ({
      appointment: { ...prevState.appointment, patient },
      additionalData: data.additionalData,
      createNewPatient: false,
    }));
  };

  handleChangeAppointmentDate = date => {
    const time = moment(this.state.appointment.expectedArrivalTime).format(
      'H:mm',
    );
    const dateStr = date.format('YYYY-MM-DD');
    const selectedDate = moment(`${dateStr} ${time}`, 'YYYY/MM/DD HH:mm');
    this.setState(prevState => ({
      appointment: {
        ...prevState.appointment,
        expectedArrivalTime: selectedDate,
      },
    }));
  };

  handleChangeAppointmentTime = date => {
    if (date) {
      const time = date.format('H:mm');
      const dateStr = moment(this.state.appointment.expectedArrivalTime).format(
        'YYYY-MM-DD',
      );
      const selectedDate = moment(`${dateStr} ${time}`, 'YYYY/MM/DD HH:mm');
      this.setState(prevState => ({
        appointment: {
          ...prevState.appointment,
          expectedArrivalTime: selectedDate,
        },
      }));
    }
  };

  handleChangeDayOffStartDate = date => {
    const time = moment(this.state.doctorDayOffObject.start).format('H:mm');
    const dateStr = date.format('YYYY-MM-DD');
    const selectedDate = moment(`${dateStr} ${time}`, 'YYYY/MM/DD HH:mm');
    this.setState(prevState => ({
      doctorDayOffObject: {
        ...prevState.doctorDayOffObject,
        start: selectedDate,
      },
    }));
  };

  handleChangeDayOffStartTime = date => {
    log.debug(date);
    if (date) {
      const time = date.format('H:mm');
      const dateStr = moment(this.state.doctorDayOffObject.start).format(
        'YYYY-MM-DD',
      );
      const selectedDate = moment(`${dateStr} ${time}`, 'YYYY/MM/DD HH:mm');
      this.setState(prevState => ({
        doctorDayOffObject: {
          ...prevState.doctorDayOffObject,
          start: selectedDate,
        },
      }));
    }
  };

  handleChangeDayOffEndDate = date => {
    const time = moment(this.state.doctorDayOffObject.end).format('H:mm');
    const dateStr = date.format('YYYY-MM-DD');
    const selectedDate = moment(`${dateStr} ${time}`, 'YYYY/MM/DD HH:mm');
    this.setState(prevState => ({
      doctorDayOffObject: {
        ...prevState.doctorDayOffObject,
        end: selectedDate,
      },
    }));
  };

  handleChangeDayOffEndTime = date => {
    if (date) {
      const time = date.format('H:mm');
      const dateStr = moment(this.state.doctorDayOffObject.end).format(
        'YYYY-MM-DD',
      );
      const selectedDate = moment(`${dateStr} ${time}`, 'YYYY/MM/DD HH:mm');
      this.setState(prevState => ({
        doctorDayOffObject: {
          ...prevState.doctorDayOffObject,
          end: selectedDate,
        },
      }));
    }
  };

  handleChangeDoctor = id => {
    log.debug(id);
    const { modalMode } = this.state;
    if (modalMode === 'appointment') {
      this.setState(prevState => ({
        appointment: { ...prevState.appointment, doctor: { id } },
      }));
    } else if (modalMode === 'doctorDayOff') {
      this.setState(prevState => ({
        doctorDayOffObject: { ...prevState.doctorDayOffObject, doctor: { id } },
      }));
    }
  };

  handleRequireTreatmentTimeChange = time => {
    this.setState(prevState => ({
      appointment: { ...prevState.appointment, requiredTreatmentTime: time },
    }));
  };

  handleCheckboxChange = (microscope, baseFloor) => {
    this.setState(prevState => ({
      appointment: { ...prevState.appointment, microscope, baseFloor },
    }));
  };

  handleNoteChange = note => {
    const { modalMode } = this.state;
    if (modalMode === 'appointment') {
      this.setState(prevState => ({
        appointment: { ...prevState.appointment, note },
      }));
    } else if (['doctorDayOff', 'clinicDayOff'].includes(modalMode)) {
      this.setState(prevState => ({
        doctorDayOffObject: { ...prevState.doctorDayOffObject, note },
      }));
    }
  };

  handleChangeModalMode = modalMode => {
    if (modalMode !== this.state.modalMode) {
      if (modalMode === 'doctorDayOff') {
        const doctorDayOffObject = emptyDoctorDayOff;
        this.setState({ doctorDayOffObject, createNewPatient: false });
      }
      if (modalMode === 'appointment') {
        const appointment = emptyAppointment;
        this.setState({
          appointment,
          createNewPatient: true,
          newPatient: emptyNewPatient,
        });
      }
      if (modalMode === 'clinicDayOff') {
        const doctorDayOffObject = emptyDoctorDayOff;
        this.setState({ doctorDayOffObject, createNewPatient: false });
      }

      this.setState({ modalMode });
    }
  };

  mapIdToDoctor = id => {
    return this.state.doctors
      .filter(doctor => doctor.id === Number(id))
      .map(doctor => ({ id: doctor.id }))[0];
  };

  handleAPIService = async () => {
    this.setState({ confirmLoading: true });
    const {
      modalMode,
      start,
      end,
      appointment,
      editEventMode,
      doctorDayOffObject,
      selectedDoctors,
    } = this.state;
    if (modalMode === 'appointment') {
      if (!editEventMode) {
        await api.createAppointment(appointment);
      } else {
        await api.editAppointment(appointment);
      }
    } else if (['doctorDayOff', 'clinicDayOff'].includes(modalMode)) {
      if (!editEventMode) {
        await api.createDoctorDayOff(doctorDayOffObject);
      } else {
        await api.editDoctorDayOff(doctorDayOffObject);
      }
    }
    setTimeout(() => {
      this.resetAppointment();
    }, 200);

    this.props.fetchAppointments(start, end, selectedDoctors);
  };

  handleChangeNewPatientBirth = date => {
    const birth = moment(date).format('YYYY-MM-DD');
    this.setState(prevState => ({
      newPatient: { ...prevState.newPatient, birth },
    }));
  };

  handleChangeNewPatientName = name => {
    this.setState(prevState => ({
      newPatient: { ...prevState.newPatient, name },
    }));
  };

  handleChangeNewPatientId = id => {
    this.setState(prevState => ({
      newPatient: { ...prevState.newPatient, nationalId: id },
    }));
  };

  handleChangeNewPatientPhone = phone => {
    this.setState(prevState => ({
      newPatient: { ...prevState.newPatient, phone },
    }));
  };

  // disposalList control
  mapLoginToDoctor = login => {
    return this.state.doctors
      .filter(doctor => doctor.login === login)
      .map(doctor => doctor.firstName)[0];
  };

  handleHeaderRender = ({ value }) => {
    const month = value.month();
    const year = value.year();
    return (
      <div>
        <Row type="flex" justify="space-between">
          <Col>
            <Button
              type="link"
              onClick={() => {
                const newValue = value.clone();
                newValue.add(-1, 'M');
                this.setState({ navigationCalendarValue: newValue }, () => {
                  let calendarApi = this.calendarComponentRef.current.getApi();
                  calendarApi.gotoDate(newValue.format('YYYY-MM-DD'));
                });
              }}
            >
              <Icon type="left" />
            </Button>
          </Col>
          <Col>
            <Title level={3}>
              {year}{month + 1}
            </Title>
          </Col>
          <Col>
            <Button
              type="link"
              onClick={() => {
                const newValue = value.clone();
                newValue.add(1, 'M');
                this.setState({ navigationCalendarValue: newValue }, () => {
                  let calendarApi = this.calendarComponentRef.current.getApi();
                  calendarApi.gotoDate(newValue.format('YYYY-MM-DD'));
                });
              }}
            >
              <Icon type="right" />
            </Button>
          </Col>
        </Row>
      </div>
    );
  };

  render() {
    // states
    const {
      visible,
      confirmModalVisible,
      displayDoctors,
      doctors,
      modalMode,
      confirmLoading,
      appointment,
      additionalData,
      confirmDropAppointment,
      confirmDropDoctorDayOff,
      confirmTitle,
      doctorDayOffObject,
      droppedEventType,
      createNewPatient,
      newPatient,
      actionType,
      deleteAppointmentId,
      deleteDoctorDayOffId,
      editEventMode,
      navigationCalendarValue,
      checkBoxDoctorsList,
      selectAllDoctorsBool,
    } = this.state;

    const { appointmentEvents, doctorStats, defaultView } = this.props;

    const {
      columnHeaderHtml,
      calendarComponentRef,
      handleDayAndViewRender,
      handleEventRender,
      handleDateClick,
      handleEventDrop,
      handleEventResize,
      handleEventClick,
      handleEventMouseEnter,
      handleEventMouseLeave,
      handleDoctorChange,
      handleDateSelect,
      handleNewAppointment,
      handleCancelModal,
      mapIdToDoctor,
      handleChangeAppointmentDate,
      handleChangeAppointmentTime,
      handleAPIService,
      handleChangeDoctor,
      handleCheckboxChange,
      handleNoteChange,
      handleSelectPatient,
      handleRequireTreatmentTimeChange,
      handleChangeModalMode,
      handleConfirmModalClose,
      handleChangeDayOffStartDate,
      handleChangeDayOffEndDate,
      handleChangeDayOffStartTime,
      handleChangeDayOffEndTime,
      handleChangeNewPatientBirth,
      handleChangeNewPatientName,
      handleChangeNewPatientId,
      handleChangeNewPatientPhone,
      handleHeaderRender,
      nextClick,
      prevClick,
      prevYearClick,
      nextYearClick,
      todayClick,
      selectAllDoctorsClick,
    } = this;

    const checkBoxDoctors = doctors
      .filter(doctor => doctor.activated)
      .map(doctor => (
        <div className="doctorCheckboxItem" key={doctor.id}>
          <Checkbox value={doctor.id}>
            {doctor.firstName} (
            {doctorStats[doctor.id] ? doctorStats[doctor.id] : 0})
          </Checkbox>
        </div>
      ));

    return (
      <div className="app-calendar" style={{ display: 'flex', width: '100%' }}>
        <FullCalendar
          eventTimeFormat={{
            hour: '2-digit',
            minute: '2-digit',
            hour12: false,
          }}
          slotLabelFormat={{
            hour: '2-digit',
            minute: '2-digit',
            hour12: false,
          }}
          slotWidth={2}
          locales={zhTW}
          locale="zh-tw"
          allDaySlot={false}
          selectable={true}
          height="parent"
          defaultView={defaultView}
          header={{
            left:
              'customPrevYearButton,customPrevButton,customNextButton,customNextYearButton, customToday',
            center: 'title',
            right: 'resourceTimeGridDay,timeGridWeek,dayGridMonth,listWeek',
          }}
          customButtons={{
            customNextButton: {
              click: nextClick,
              icon: 'chevron-right',
            },
            customPrevButton: {
              click: prevClick,
              icon: 'chevron-left',
            },
            customPrevYearButton: {
              click: prevYearClick,
              icon: 'chevrons-left',
            },
            customNextYearButton: {
              click: nextYearClick,
              icon: 'chevrons-right',
            },
            customToday: {
              click: todayClick,
              text: '',
            },
          }}
          titleFormat={{ year: 'numeric', month: 'numeric', day: 'numeric' }}
          titleRangeSeparator=" ~ "
          views={{
            resourceTimeGridDay: {
              type: 'resourceTimeGrid',
              duration: { days: 1 },
              buttonText: 'day',
            },
            timeGridWeek: { columnHeaderHtml: columnHeaderHtml },
            month: {
              eventLimit: 3,
            },
          }}
          buttonText={{
            resourceTimeGridDay: '',
            listWeek: '',
          }}
          plugins={[
            dayGridPlugin,
            timeGridPlugin,
            interactionPlugin,
            resourceTimeGridPlugin,
            listPlugin,
          ]}
          ref={calendarComponentRef}
          events={appointmentEvents}
          resources={doctors.map(doctor => ({
            id: doctor.login,
            title: doctor.firstName,
          }))}
          businessHours={{
            daysOfWeek: [0, 1, 2, 3, 4, 5, 6],
            startTime: '08:00',
            endTime: '22:00',
          }}
          minTime="08:00:00"
          maxTime="22:00:00"
          scrollTime="08:30:00"
          datesRender={handleDayAndViewRender}
          navLinks
          editable
          droppable
          datesAboveResources
          nowIndicator
          eventLimit
          eventRender={handleEventRender}
          dateClick={handleDateClick}
          eventDrop={handleEventDrop}
          eventResize={handleEventResize}
          eventClick={handleEventClick}
          eventMouseEnter={handleEventMouseEnter}
          eventMouseLeave={handleEventMouseLeave}
          schedulerLicenseKey="GPL-My-Project-Is-Open-Source"
          slotDuration="00:05:00"
          slotLabelInterval={{ hours: 1 }}
        />
        <div
          style={{
            position: 'relative',
            padding: '0 0 0 20px',
            display: 'flex',
            flexDirection: 'column',
            color: '#000',
          }}
        >
          <div style={{ width: '90%', margin: '17px auto 30px' }}>
            <Button
              type="primary"
              size={'large'}
              onClick={handleNewAppointment}
              block
            >
              
            </Button>
          </div>
          <div
            style={{ marginTop: '10px', marginBottom: '10px', width: '300px' }}
          >
            <Calendar
              fullscreen={false}
              onSelect={handleDateSelect}
              mode="month"
              headerRender={handleHeaderRender}
              value={navigationCalendarValue}
            />
          </div>
          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <p
              style={{ fontSize: '16px', fontWeight: '800', textAlign: 'left' }}
            >
              
            </p>
            <Checkbox
              checked={selectAllDoctorsBool}
              onChange={selectAllDoctorsClick}
            >
              
            </Checkbox>
          </div>
          <div style={{ overflow: 'scroll', display: 'flex' }}>
            <Checkbox.Group
              onChange={handleDoctorChange}
              value={checkBoxDoctorsList}
            >
              {checkBoxDoctors}
            </Checkbox.Group>
          </div>
        </div>
        <AppointmentModal
          modalMode={modalMode}
          visible={visible}
          doctors={doctors}
          displayDoctors={displayDoctors}
          handleCancelModal={handleCancelModal}
          mapIdToDoctor={mapIdToDoctor}
          appointment={appointment}
          additionalData={additionalData}
          confirmLoading={confirmLoading}
          handleChangeAppointmentDate={handleChangeAppointmentDate}
          handleChangeAppointmentTime={handleChangeAppointmentTime}
          APIService={handleAPIService}
          handleChangeDoctor={handleChangeDoctor}
          handleCheckboxChange={handleCheckboxChange}
          handleNoteChange={handleNoteChange}
          handleSelectPatient={handleSelectPatient}
          handleRequireTreatmentTimeChange={handleRequireTreatmentTimeChange}
          handleChangeModalMode={handleChangeModalMode}
          doctorDayOffObject={doctorDayOffObject}
          handleChangeDayOffStartDate={handleChangeDayOffStartDate}
          handleChangeDayOffEndDate={handleChangeDayOffEndDate}
          handleChangeDayOffStartTime={handleChangeDayOffStartTime}
          handleChangeDayOffEndTime={handleChangeDayOffEndTime}
          createNewPatient={createNewPatient}
          newPatient={newPatient}
          handleChangeNewPatientBirth={handleChangeNewPatientBirth}
          handleChangeNewPatientName={handleChangeNewPatientName}
          handleChangeNewPatientPhone={handleChangeNewPatientPhone}
          handleChangeNewPatientId={handleChangeNewPatientId}
          editEventMode={editEventMode}
        />
        <ConfirmModal
          visible={confirmModalVisible}
          confirmDropAppointment={confirmDropAppointment}
          confirmDropDoctorDayOff={confirmDropDoctorDayOff}
          confirmTitle={confirmTitle}
          handleConfirmModalClose={handleConfirmModalClose}
          droppedEventType={droppedEventType}
          actionType={actionType}
          deleteAppointmentId={deleteAppointmentId}
          deleteDoctorDayOffId={deleteDoctorDayOffId}
        />
      </div>
    );
  }
}
