import moment from 'moment';

export function convertAppointmentsToEvent(appointments) {
  const now = moment();
  if (appointments.length > 0) {
    return appointments
      .filter(appointment => {
        if (appointment.registration) {
          return !(
            appointment.registration.arrivalTime ===
            appointment.expectedArrivalTime
          );
        }
        return true;
      })
      .map(appointment => {
        const start = appointment.expectedArrivalTime;
        const requiredTreatmentTime =
          appointment.requiredTreatmentTime === 0
            ? 15
            : appointment.requiredTreatmentTime;
        return {
          title: appointment.patient.name,
          resourceId: appointment.doctor.user.login,
          start: moment(start).toDate(),
          end: moment(start)
            .add(requiredTreatmentTime, 'minutes')
            .toDate(),
          color: convertStatusToColor(appointment, now),
          appointment: {
            expectedArrivalTime: appointment.expectedArrivalTime,
            patient: {
              id: appointment.patient.id,
              newPatient: appointment.patient.newPatient,
            },
            doctor: { id: appointment.doctor.id },
            requiredTreatmentTime: appointment.requiredTreatmentTime,
            microscope: appointment.microscope ? appointment.microscope : false,
            baseFloor: appointment.baseFloor ? appointment.baseFloor : false,
            note: appointment.note ? appointment.note : '',
            id: appointment.id,
          },
          additionalData: {
            name: appointment.patient.name,
            birth: appointment.patient.birth,
            gender: appointment.patient.gender,
            age: appointment.patient.birth
              ? moment().diff(moment(appointment.patient.birth), 'years')
              : undefined,
            phone: appointment.patient.phone,
            doctor: appointment.doctor.user.firstName,
            lastModifiedDate: appointment.patient.lastModifiedDate
              ? moment(appointment.patient.lastModifiedDate).format(
                  'YYYY-MM-DD',
                )
              : '',
            lastModifiedBy: appointment.patient.lastModifiedBy,
          },
          eventType: 'appointment',
        };
      });
  } else {
    return [];
  }
}

export function convertDoctorDayOffToEvent(doctorDayOffs) {
  return doctorDayOffs.map(doctorDayOff => {
    let doctorDayOffObject;
    let eventType;
    if (doctorDayOff.doctor) {
      doctorDayOffObject = {
        id: doctorDayOff.id,
        doctor: { id: doctorDayOff.doctor.id },
        start: doctorDayOff.start,
        end: doctorDayOff.end,
        note: doctorDayOff.note,
        timeType: doctorDayOff.timeType,
      };
      eventType = 'doctorDayOff';
    } else {
      doctorDayOffObject = {
        id: doctorDayOff.id,
        start: doctorDayOff.start,
        end: doctorDayOff.end,
        note: doctorDayOff.note,
        timeType: doctorDayOff.timeType,
      };
      eventType = 'clinicDayOff';
    }
    return {
      title: doctorDayOff.doctor
        ? doctorDayOff.doctor.user.firstName
        : '',
      start: moment(doctorDayOff.start).toDate(),
      end: moment(doctorDayOff.end).toDate(),
      color: '#dfdfdf',
      doctorDayOffObject,
      eventType,
    };
  });
}

function convertStatusToColor(appointment, now) {
  if (appointment.registration) {
    if (appointment.registration.status === 'PENDING') {
      return '#4da1ff';
    }
    if (appointment.registration.status === 'IN_PROGRESS') {
      return '#26bc97';
    }
    if (appointment.registration.status === 'FINISHED') {
      return '#c2cbd5';
    }
  }

  const isBefore = now.isBefore(moment(appointment.expectedArrivalTime));
  const status = appointment.status;

  if (status === 'CONFIRMED') {
    if (isBefore) {
      return '#fcb754';
    }
    return '#c2cbd5';
  }

  if (status === 'CANCEL') {
    return '#c2cbd5';
  }
  //
  if (isBefore) {
    return '#fcb754';
  }
  return '#c2cbd5';
}

export function convertPatientToAutoCompletePatient(patients) {
  if (patients.length > 0) {
    return patients.map(patient => {
      patient.value = patient.medicalId;
      patient.text = `${patient.name}, ${patient.medicalId}`;
      return patient;
    });
  } else {
    return [];
  }
}

export function convertSettingsToBackgroundEvents(settings, start, end) {
  const {
    mon,
    tue,
    wed,
    thu,
    fri,
    sat,
    sun,
    morningStartTime,
    morningEndTime,
    noonStartTime,
    noonEndTime,
    eveningStartTime,
    eveningEndTime,
    nightStartTime,
    nightEndTime,
  } = settings.preferences.generalSetting;
  const timeSetting = {
    morningStartTime,
    morningEndTime,
    noonStartTime,
    noonEndTime,
    eveningStartTime,
    eveningEndTime,
    nightStartTime,
    nightEndTime,
  };
  let backgroundEvent = [];
  for (let date = moment(start); date.isBefore(end); date.add(1, 'days')) {
    const dayOfWeek = date.day();
    switch (dayOfWeek) {
      case 0:
        backgroundEvent = [
          ...backgroundEvent,
          ...handleGenerateBackgroundEvent(date, sun, timeSetting),
        ];
        break;
      case 1:
        backgroundEvent = [
          ...backgroundEvent,
          ...handleGenerateBackgroundEvent(date, mon, timeSetting),
        ];
        break;
      case 2:
        backgroundEvent = [
          ...backgroundEvent,
          ...handleGenerateBackgroundEvent(date, tue, timeSetting),
        ];
        break;
      case 3:
        backgroundEvent = [
          ...backgroundEvent,
          ...handleGenerateBackgroundEvent(date, wed, timeSetting),
        ];
        break;
      case 4:
        backgroundEvent = [
          ...backgroundEvent,
          ...handleGenerateBackgroundEvent(date, thu, timeSetting),
        ];
        break;
      case 5:
        backgroundEvent = [
          ...backgroundEvent,
          ...handleGenerateBackgroundEvent(date, fri, timeSetting),
        ];
        break;
      case 6:
        backgroundEvent = [
          ...backgroundEvent,
          ...handleGenerateBackgroundEvent(date, sat, timeSetting),
        ];
        break;
      default:
        break;
    }
  }
  return backgroundEvent;
}

function handleGenerateBackgroundEvent(date, daySetting, timeSetting) {
  let backgroundEvent = [];
  if (!daySetting.activated) {
    backgroundEvent.push(generateBackgroundEvent(true, date));
  } else {
    backgroundEvent = [
      ...backgroundEvent,
      ...calculateBackgroundEvent(date, daySetting, timeSetting),
    ];
  }
  return backgroundEvent;
}

export function generateBackgroundEvent(allDay, start, end) {
  if (allDay) {
    return {
      start: start.format('YYYY-MM-DD'),
      end: start.format('YYYY-MM-DD'),
      rendering: 'background',
      color: '#ddd',
      eventType: 'regularDayOff',
    };
  }
  return {
    start: start.format('YYYY-MM-DD HH:mm'),
    end: end.format('YYYY-MM-DD HH:mm'),
    rendering: 'background',
    color: '#ddd',
    eventType: 'regularDayOff',
  };
}

export function calculateBackgroundEvent(date, daySetting, timeSetting) {
  let backgroundEvent = [];
  const {
    morningStartTime,
    morningEndTime,
    noonStartTime,
    noonEndTime,
    eveningStartTime,
    eveningEndTime,
  } = timeSetting;
  const { morningShift, noonShift, eveningShift } = daySetting;
  const dateString = date.format('YYYY-MM-DD');
  let startTime = moment(date).startOf('d');
  let endTime;

  if (morningShift) {
    endTime = moment(`${dateString} ${morningStartTime}`);
    backgroundEvent.push(generateBackgroundEvent(false, startTime, endTime));
    startTime = moment(`${dateString} ${morningEndTime}`);
  }

  if (noonShift) {
    endTime = moment(`${dateString} ${noonStartTime}`);
    backgroundEvent.push(generateBackgroundEvent(false, startTime, endTime));
    startTime = moment(`${dateString} ${noonEndTime}`);
  }

  if (eveningShift) {
    endTime = moment(`${dateString} ${eveningStartTime}`);
    backgroundEvent.push(generateBackgroundEvent(false, startTime, endTime));
    startTime = moment(`${dateString} ${eveningEndTime}`);
  }

  endTime = moment(date).endOf('d');
  backgroundEvent.push(generateBackgroundEvent(false, startTime, endTime));

  return backgroundEvent;
}

export function checkCreateNewPatientDataCompleteness(data) {
  if (data.name.length === 0) {
    return { ok: false, label: '' };
  } else if (data.phone.length === 0) {
    return { ok: false, label: '' };
  } else if (data.birth.length === 0) {
    return { ok: false, label: '' };
  }
  return { ok: true };
}

export function checkAppointmentDataCompleteness(data) {
  const expectedArrivalTimeValidation = moment(
    data.expectedArrivalTime,
    'YYYY-MM-DD',
  ).isValid();
  const doctorValidation = data.doctor.id ? true : false;
  const requiredTreatmentTimeValidation = data.requiredTreatmentTime
    ? true
    : false;
  if (!expectedArrivalTimeValidation) {
    return { ok: false, label: '' };
  } else if (!doctorValidation) {
    return { ok: false, label: '' };
  } else if (!requiredTreatmentTimeValidation) {
    return { ok: false, label: '' };
  }
  return { ok: true };
}

export function checkDoctorDayOffDataCompleteness(data) {
  const startTimeValidation = moment(data.start, 'YYYY-MM-DD').isValid();
  const endTimeValidation = moment(data.end, 'YYYY-MM-DD').isValid();
  const doctorValidation = data.doctor && data.doctor.id;
  if (!startTimeValidation) {
    return { ok: false, label: '' };
  } else if (!endTimeValidation) {
    return { ok: false, label: '' };
  } else if (!doctorValidation) {
    return { ok: false, label: '' };
  }
  return { ok: true };
}

export function checkClinicDayOffDataCompleteness(data) {
  const startTimeValidation = moment(data.start, 'YYYY-MM-DD').isValid();
  const endTimeValidation = moment(data.end, 'YYYY-MM-DD').isValid();
  if (!startTimeValidation) {
    return { ok: false, label: '' };
  } else if (!endTimeValidation) {
    return { ok: false, label: '' };
  }
  return { ok: true };
}

export function translateGender(gender) {
  if (gender === 'MALE') {
    return '';
  }
  if (gender === 'FEMALE') {
    return '';
  }
  return '';
}
