import axios from 'axios';
import { Cookies } from 'react-cookie';

const cookies = new Cookies();

let apiUrl = 'http://localhost:8080';
if (window) {
  const currentLocation = window.location;
  apiUrl =
    'https://' + currentLocation.hostname + ':' + currentLocation.port + '/api';
}

const instance = axios.create({
  baseURL: apiUrl,
});
export default instance;

export async function greeting() {
  let response;
  try {
    response = await instance.get('/greeting');
  } catch (ex) {
    return undefined;
  }
  return response;
}

export async function login(username, password, remember) {
  const data = {
    username: username,
    password: password,
    rememberMe: remember,
  };
  let response;
  try {
    response = await instance.post('/authenticate', data);
  } catch (ex) {
    return undefined;
  }
  return response;
}

export async function validate(token) {
  let response;
  try {
    response = await instance.get('/authenticate', {
      headers: {
        Authorization: 'Bearer ' + token,
      },
    });
  } catch (ex) {
    return undefined;
  }
  return response;
}

export async function getDoctors() {
  const token = cookies.get('token') || undefined;
  const query = '?size=50';
  let response;
  try {
    response = await instance.get('/users' + query, {
      headers: {
        Authorization: 'Bearer ' + token,
      },
    });
  } catch (ex) {
    return undefined;
  }
  return response;
}

/**
 * Get appointments based on start and end days
 * @param start moment object
 * @param end moment object
 * @returns {Promise<AxiosResponse<T>|undefined>}
 */
export async function getAppointments(start, end, doctors) {
  doctors = doctors ? doctors : [];
  const token = cookies.get('token') || undefined;
  let query =
    '?expectedArrivalTime.greaterOrEqualThan=' +
    start.toISOString() +
    '&expectedArrivalTime.lessThan=' +
    end.toISOString() +
    '&sort=expectedArrivalTime&size=200';
  if (doctors.length > 0) {
    query += `&doctorId.in=${doctors}`;
  }

  let response;
  try {
    response = await instance.get('/appointments' + query, {
      headers: {
        Authorization: 'Bearer ' + token,
      },
    });
  } catch (ex) {
    return undefined;
  }
  return response;
}

