import React from 'react';
import { instanceOf } from 'prop-types';
import LoginForm from '../component/LoginForm';
import AppointmentScheduler from '../component/AppointmentScheduler';
import {
  Avatar,
  Menu,
  Dropdown,
  Typography,
  Button,
  Icon,
  Layout,
  Empty,
} from 'antd';
import { withCookies, Cookies } from 'react-cookie';
import * as api from '../util/api';
import * as log from 'loglevel';
import * as helpers from '../util/helpers';
import NProgress from 'nprogress';

import 'nprogress/nprogress.css';
import './Home.css';
import Logo from '../static/images/logo_dentall_icon.svg';

const { Title } = Typography;

class Home extends React.Component {
  static propTypes = {
    cookies: instanceOf(Cookies).isRequired,
  };

  constructor(props) {
    super(props);

    const { cookies } = props;
    this.state = {
      token: cookies.get('token') || undefined,
      defaultView: cookies.get('defaultView') || 'timeGridWeek',
      isServerAlive: undefined,
      isLoggedIn: undefined,
      fetchTimes: 0,
      user: null,
      doctorStats: {},
      clinic: '',
    };
  }

  async componentDidMount() {
    log.debug('token', this.state.token);
    const search = window.location.search;
    const params = new URLSearchParams(search);
    const clinic = params.get('clinic');
    if (this.state.token) {
      const response = await api.validate(this.state.token);
      if (response) {
        log.debug('user', response.data);
        if (response.data) {
          this.setState({
            isLoggedIn: true,
            isServerAlive: true,
            user: response.data,
          });
        } else {
          this.setState({ isLoggedIn: false, isServerAlive: true, clinic });
        }
      }
    } else {
      const response = await api.greeting();
      if (clinic) {
        this.setState({
          isServerAlive: response && response.status === 200,
          clinic,
        });
      } else {
        this.setState({ isServerAlive: response && response.status === 200 });
      }
    }
  }

  fetchAppointments = async (start, end, doctors) => {
    log.debug(doctors);
    NProgress.start();
    const doctorDayOff = await api.getDoctorDayOff(start, end);
    let doctorDayOffEvent = doctorDayOff
      ? helpers.convertDoctorDayOffToEvent(doctorDayOff.data)
      : [];
    if (doctors && doctors.length > 0) {
      doctorDayOffEvent = doctorDayOffEvent.filter(event => {
        if (!event.doctorDayOffObject.doctor) return true;
        return (
          doctors.indexOf(event.doctorDayOffObject.doctor.id.toString()) !== -1
        );
      });
    }
    log.debug(doctorDayOffEvent);
    const appointments =
      doctors === 'doNotQuery'
        ? { data: [] }
        : await api.getAppointments(start, end, doctors);
    let appointmentEvents = appointments
      ? helpers.convertAppointmentsToEvent(appointments.data)
      : [];

    let doctorStats = {};
    appointmentEvents.forEach(appointmentEvent => {
      if (!appointmentEvent.appointment.doctor) {
        return;
      }
      const id = appointmentEvent.appointment.doctor.id;
      if (doctorStats[id]) {
        doctorStats[id]++;
      } else {
        doctorStats[id] = 1;
      }
    });

    const settings = await api.getCalendarEvent();
    const calendarEvent = helpers.convertSettingsToBackgroundEvents(
      settings.data[0],
      start,
      end,
    );

    appointmentEvents = [
      ...appointmentEvents,
      { title: this.state.fetchTimes.toString() },
      ...doctorDayOffEvent,
      ...calendarEvent,
    ];
    const fetchTimes = ++this.state.fetchTimes;
    this.setState({ appointmentEvents, fetchTimes, doctorStats });
    NProgress.done();
  };

  handleOnLoggedIn = (data, username) => {
    const { cookies } = this.props;
    cookies.set('token', data.id_token, { path: '/' });
    this.setState({ token: data.id_token, isLoggedIn: true, user: username });
    log.debug(data);
  };

  handleLogoutClick = e => {
    const { cookies } = this.props;
    cookies.set('token', undefined, { path: '/' });
    this.setState({ isLoggedIn: false });
  };

  render() {
    const {
      user,
      isServerAlive,
      isLoggedIn,
      appointmentEvents,
      doctorStats,
      clinic,
      defaultView,
    } = this.state;

    const { cookies } = this.props;

    if (isServerAlive === undefined) return null;
    if (!isServerAlive) return <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />;

    if (!isLoggedIn)
      return (
        <div className="login-form-wrapper">
          <Title>{clinic}</Title>
          <LoginForm onLoggedIn={this.handleOnLoggedIn} />
        </div>
      );

    const { Header, Content } = Layout;

    const headerStyle = {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'space-between',
      zIndex: 1,
      height: 64,
      width: '100%',
      padding: '0 10px',
      background: '#fff',
      boxShadow: '0 1px 5px 0 #d7e3f1',
    };

    const menu = (
      <Menu>
        <Menu.Item>
          <Button type="default" onClick={this.handleLogoutClick}>
            <Icon type="logout" />
            
          </Button>
        </Menu.Item>
      </Menu>
    );

    return (
      <Layout style={{ height: '100vh', backgroundColor: 'white' }}>
        <Header style={headerStyle}>
          <div style={{ display: 'flex', marginLeft: '37px' }}>
            <img
              src={Logo}
              height={24}
              alt="Logo"
              style={{ marginRight: '6px' }}
            />
            <Title level={4} style={{ color: '#00a1e1', margin: 'auto' }}>
              
            </Title>
          </div>
          <Dropdown overlay={menu}>
            <div
              style={{
                color: '#4a4a4a',
                cursor: 'pointer',
                textAlign: 'center',
                paddingRight: '29px',
                paddingLeft: '10px',
              }}
            >
              <Avatar
                style={{
                  backgroundColor: '#7265e6',
                  verticalAlign: 'middle',
                  marginRight: '5px',
                }}
                size="medium"
              >
                {user && user.charAt(0).toUpperCase()}
              </Avatar>
              {user}
            </div>
          </Dropdown>
        </Header>
        <Content
          style={{
            display: 'flex',
            alignItems: 'stretch',
          }}
        >
          <AppointmentScheduler
            appointmentEvents={appointmentEvents}
            fetchAppointments={this.fetchAppointments}
            doctorStats={doctorStats}
            cookies={cookies}
            defaultView={defaultView}
          />
        </Content>
      </Layout>
    );
  }
}

export default withCookies(Home);
