import {
  Modal,
  AutoComplete,
  Input,
  Icon,
  Select,
  Checkbox,
  DatePicker,
  TimePicker,
  Button,
  message,
} from 'antd';
import React from 'react';
import * as api from '../util/api';
import * as helpers from '../util/helpers';
import * as log from 'loglevel';
import moment from 'moment';
import './AppointmentModal.css';
import manAvatar from '../static/images/man@2x.png';
import womanAvatar from '../static/images/woman@2x.png';
import defaultAvatar from '../static/images/Default.png';

export const requiredTreatmentTimeOptionValue = [
  10,
  15,
  45,
  60,
  75,
  90,
  105,
  120,
];
export default class AppointmentModal extends React.Component {
  state = {
    timePickerOpen: false,
    timePickerOpen2: false,
    timePickerOpen3: false,
    patients: [],
    searchPatientString: '',
    timePickerDisabled: false,
  };

  delayTimer = null;
  handleSearchPatient = searchPatientString => {
    this.setState({ searchPatientString });
    clearTimeout(this.delayTimer);
    this.delayTimer = setTimeout(async () => {
      const response = await api.getPatients(searchPatientString);
      if (response && response.data) {
        this.setState({ patients: response.data });
      }
    }, 500);
  };

  handleSearchPatientSelected = (value, option) => {
    return this.state.patients
      .filter(patient => patient.medicalId === value)
      .map(patient => ({
        patient: { id: patient.id, newPatient: patient.newPatient },
        additionalData: {
          name: patient.name,
          gender: patient.gender,
          birth: patient.birth,
          age: patient.birth
            ? moment().diff(moment(patient.birth), 'years')
            : 0,
          phone: patient.phone,
          lastModifiedDate: patient.lastModifiedDate
            ? moment(patient.lastModifiedDate).format('YYYY-MM-DD')
            : '',
          lastModifiedBy: patient.lastModifiedBy,
        },
      }))[0];
  };

  readCheckbox = value => {
    log.debug('checked = ', value);
    const microscope = value.includes('MICRO') ? true : false;
    const baseFloor = value.includes('baseFloor') ? true : false;
    return { microscope, baseFloor };
  };

  handleOk = async () => {
    if (this.props.createNewPatient) {
      const completeness = helpers.checkCreateNewPatientDataCompleteness(
        this.props.newPatient,
      );
      if (completeness.ok) {
        try {
          const response = await api.createPatient(this.props.newPatient);
          const data = response.data;
          const patient = {
            patient: { id: data.id, newPatient: data.newPatient },
            additionalData: {
              name: data.name,
              gender: data.gender,
              birth: data.birth,
              age: data.birth ? moment().diff(moment(data.birth), 'years') : 0,
              phone: data.phone,
              lastModifiedDate: data.lastModifiedDate
                ? moment(data.lastModifiedDate).format('YYYY-MM-DD')
                : '',
              lastModifiedBy: data.lastModifiedBy,
            },
          };
          this.props.handleSelectPatient(patient);
          const completeness = helpers.checkAppointmentDataCompleteness(
            this.props.appointment,
          );
          if (!completeness.ok) {
            this.messageShow(completeness.label);
            return;
          }
          this.props.APIService();
        } catch {}
      } else {
        this.messageShow(completeness.label);
      }
    } else {
      const modalMode = this.props.modalMode;
      if (modalMode === 'appointment') {
        const completeness = helpers.checkAppointmentDataCompleteness(
          this.props.appointment,
        );
        if (!completeness.ok) {
          this.messageShow(completeness.label);
          return;
        }
      } else if (modalMode === 'doctorDayOff') {
        const completeness = helpers.checkDoctorDayOffDataCompleteness(
          this.props.doctorDayOffObject,
        );
        if (!completeness.ok) {
          this.messageShow(completeness.label);
          return;
        }
      } else if (modalMode === 'clinicDayOff') {
        const completeness = helpers.checkClinicDayOffDataCompleteness(
          this.props.doctorDayOffObject,
        );
        if (!completeness.ok) {
          this.messageShow(completeness.label);
          return;
        }
      }

      this.props.APIService();
    }
    this.resetModal();
  };

  messageShow = content => {
    message.error(`${content}`);
  };

  handleCancel = () => {
    log.debug('Clicked cancel button');
    this.props.handleCancelModal();
    this.resetModal();
  };

  resetModal = () => {
    this.setState({ searchPatientString: '' });
  };

  handleTimePickerClose = () =>
    this.setState({
      timePickerOpen: false,
      timePickerOpen2: false,
      timePickerOpen3: false,
    });

  handleTimePickerOpen = () =>
    this.setState(prevState => ({ timePickerOpen: !prevState.timePickerOpen }));

  handleTimePickerOpen2 = () =>
    this.setState(prevState => ({
      timePickerOpen2: !prevState.timePickerOpen2,
    }));

  handleTimePickerOpen3 = () =>
    this.setState(prevState => ({
      timePickerOpen3: !prevState.timePickerOpen3,
    }));

  handleModalModeChange = modalMode => {
    log.debug(modalMode);
    this.props.handleChangeModalMode(modalMode);
  };

  handleAllDayButtonChange = e => {
    log.debug(e.target.checked);
    this.props.handleChangeDayOffStartTime(moment().startOf('day'));
    this.props.handleChangeDayOffEndTime(moment().endOf('day'));
    this.setState({ timePickerDisabled: e.target.checked });
  };

  render() {
    const {
      searchPatientString,
      patients,
      timePickerOpen,
      timePickerOpen2,
      timePickerOpen3,
      timePickerDisabled,
    } = this.state;
    const {
      // data
      visible,
      displayDoctors,
      modalMode,
      appointment,
      additionalData,
      confirmLoading,
      doctors,
      doctorDayOffObject,
      createNewPatient,
      newPatient,
      editEventMode,
    } = this.props;
    // method
    const {
      handleChangeModalMode,
      handleChangeDoctor,
      handleRequireTreatmentTimeChange,
      handleNoteChange,
      handleChangeAppointmentDate,
      handleChangeAppointmentTime,
      handleSelectPatient,
      handleCheckboxChange,
      handleChangeDayOffStartDate,
      handleChangeDayOffStartTime,
      handleChangeDayOffEndDate,
      handleChangeDayOffEndTime,
      handleChangeNewPatientBirth,
      handleChangeNewPatientName,
      handleChangeNewPatientPhone,
      handleChangeNewPatientId,
    } = this.props;
    const {
      handleCancel,
      handleOk,
      handleSearchPatient,
      handleSearchPatientSelected,
      readCheckbox,
      handleTimePickerOpen,
      handleTimePickerOpen2,
      handleTimePickerOpen3,
      handleTimePickerClose,
      handleAllDayButtonChange,
    } = this;
    const { Option } = Select;

    let modalArgs = {};

    modalArgs.title = (
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        {editEventMode && <span></span>}
        {!editEventMode && (
          <>
            <span></span>
            <Select
              value={modalMode}
              style={{ width: 120 }}
              onSelect={modalMode => {
                handleChangeModalMode(modalMode);
              }}
            >
              <Option value="appointment"></Option>
              <Option value="doctorDayOff"></Option>
              <Option value="clinicDayOff"></Option>
            </Select>
          </>
        )}
      </div>
    );

    modalArgs.date = moment(appointment.expectedArrivalTime);
    modalArgs.dayOffDateStart = moment(doctorDayOffObject.start);
    modalArgs.dayOffDateEnd = moment(doctorDayOffObject.end);

    modalArgs.footer = (
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        <div>
          <Button key="back" onClick={handleCancel}>
            
          </Button>
          <Button
            key="submit"
            type="primary"
            loading={confirmLoading}
            onClick={handleOk}
          >
            {editEventMode ? '' : ''}
          </Button>
        </div>
      </div>
    );

    let checkboxValue = [];
    if (appointment.microscope === true) {
      checkboxValue.push('MICRO');
    }
    if (appointment.baseFloor === true) {
      checkboxValue.push('baseFloor');
    }
    const { TextArea } = Input;
    const options = [
      { label: 'micro', value: 'MICRO' },
      { label: '', value: 'baseFloor' },
    ];

    let doctorValue;
    if (modalMode === 'appointment') {
      doctorValue = appointment.doctor.id
        ? doctors.filter(doctor => doctor.id === appointment.doctor.id)[0]
            .firstName
        : undefined;
    } else if (modalMode === 'doctorDayOff') {
      doctorValue = doctorDayOffObject.doctor
        ? doctors.filter(
            doctor => doctor.id === doctorDayOffObject.doctor.id,
          )[0].firstName
        : undefined;
    }

    let avatar;
    if (additionalData.gender === 'MALE') {
      avatar = manAvatar;
    } else if (additionalData.gender === 'FEMALE') {
      avatar = womanAvatar;
    } else {
      avatar = defaultAvatar;
    }

    return (
      <Modal
        title={modalArgs.title}
        footer={modalArgs.footer}
        visible={visible}
        onOk={handleOk}
        confirmLoading={confirmLoading}
        onCancel={handleCancel}
        width={718}
        centered
        closable={false}
        destroyOnClose
      >
        {modalMode === 'appointment' && (
          <div>
            {!editEventMode && (
              <AutoComplete
                style={{ width: '100%', marginBottom: '20px' }}
                dataSource={
                  patients
                    ? helpers.convertPatientToAutoCompletePatient(patients)
                    : []
                }
                placeholder="Please enter patient name"
                onChange={handleSearchPatient}
                value={searchPatientString}
                onSelect={(value, option) => {
                  handleSelectPatient(handleSearchPatientSelected(value));
                }}
              >
                <Input
                  suffix={
                    <Icon type="search" className="certain-category-icon" />
                  }
                />
              </AutoComplete>
            )}
            {!createNewPatient && (
              <div className="appointmentModalPatientInfo">
                <img
                  src={avatar}
                  className="appointmentAvatar"
                  alt=""
                />
                <div
                  style={{
                    width: '100%',
                    display: 'flex',
                  }}
                >
                  <div style={{ marginRight: '60px' }} className="patientInfo">
                    <p>{additionalData.name ? additionalData.name : 'name'}</p>
                    <p>{`${
                      additionalData.birth ? additionalData.birth : 'birth'
                    }, ${additionalData ? additionalData.age : 'age'}, ${
                      additionalData.gender ? additionalData.gender : 'gender'
                    }`}</p>
                    <p>: 2, : 1, : 5</p>
                  </div>
                  <div className="patientInfo">
                    <p>
                      :{' '}
                      {additionalData.lastModifiedDate
                        ? additionalData.lastModifiedDate
                        : ''}
                      (
                      {additionalData.lastModifiedBy
                        ? additionalData.lastModifiedBy
                        : ''}
                      )
                    </p>
                    <p>: 2019/10/10(DR. B)</p>
                    <p>: 2020/10/12</p>
                  </div>
                </div>
              </div>
            )}
            {createNewPatient && (
              <div className="appointmentModalPatientInfo">
                <img
                  src={defaultAvatar}
                  className="appointmentAvatar"
                  alt=""
                />
                <div>
                  <div>
                    <span className="appModalLabel">:</span>
                    <Input
                      placeholder="*Patient Name"
                      style={{
                        width: '170px',
                        marginBottom: '5px',
                        marginRight: '5px',
                      }}
                      value={newPatient.name}
                      onChange={e => {
                        log.debug(e.target.value);
                        handleChangeNewPatientName(e.target.value);
                      }}
                    />
                    <span className="appModalLabel appointmentModalLastLabel">
                      :
                    </span>
                    <DatePicker
                      allowClear={false}
                      placeholder="*birth"
                      onChange={(date, dateString) => {
                        if (moment(dateString)._isValid) {
                          handleChangeNewPatientBirth(moment(dateString));
                        }
                      }}
                      value={moment(newPatient.birth)}
                      style={{
                        marginBottom: '5px',
                        marginRight: '5px',
                        height: '32px',
                        width: '170px',
                      }}
                    />
                  </div>
                  <div>
                    <span className="appModalLabel">:</span>
                    <Input
                      style={{
                        width: '170px',
                        marginBottom: '5px',
                        marginRight: '5px',
                      }}
                      placeholder="ID"
                      value={newPatient.nationalId}
                      onChange={e => {
                        handleChangeNewPatientId(e.target.value);
                      }}
                    />
                    <span className="appModalLabel appointmentModalLastLabel">
                      :
                    </span>
                    <Input
                      style={{
                        width: '170px',
                        marginBottom: '5px',
                        marginRight: '5px',
                      }}
                      placeholder="*Phone"
                      value={newPatient.phone}
                      onChange={e => {
                        handleChangeNewPatientPhone(e.target.value);
                      }}
                    />
                  </div>
                </div>
              </div>
            )}
            <br />
            <div style={{ display: 'flex' }}>
              <p style={{ width: '100px' }}>: </p>
              <DatePicker
                allowClear={false}
                onChange={(date, dateString) => {
                  if (moment(dateString)._isValid) {
                    handleChangeAppointmentDate(moment(dateString));
                  }
                }}
                value={modalArgs.date}
                style={{ marginRight: '13px' }}
              />
              <TimePicker
                allowClear={false}
                value={modalArgs.date}
                open={timePickerOpen}
                onOpenChange={handleTimePickerOpen}
                onChange={time => {
                  handleChangeAppointmentTime(time);
                }}
                format="HH:mm"
                addon={() => (
                  <Button
                    size="small"
                    type="primary"
                    onClick={handleTimePickerClose}
                  >
                    Ok
                  </Button>
                )}
              />
            </div>
            <br />
            <div style={{ display: 'flex' }}>
              <p style={{ width: '100px' }}>: </p>
              <Select
                style={{ width: 300 }}
                placeholder=""
                onSelect={value => {
                  handleChangeDoctor(Number(value));
                }}
                value={doctorValue}
              >
                {displayDoctors}
              </Select>
            </div>
            <br />
            <div style={{ display: 'flex' }}>
              <p style={{ width: '100px' }}>: </p>
              <Select
                style={{ width: 100 }}
                placeholder="Please select a time period"
                onChange={value => {
                  handleRequireTreatmentTimeChange(value);
                }}
                value={appointment.requiredTreatmentTime}
              >
                {requiredTreatmentTimeOptionValue.map(option => {
                  return <Option value={option}>{option}</Option>;
                })}
              </Select>
            </div>
            <br />
            <div style={{ display: 'flex' }}>
              <p style={{ width: '100px' }}>: </p>
            </div>
            <TextArea
              rows={4}
              onChange={e => {
                handleNoteChange(e.target.value);
              }}
              value={appointment.note}
            />
            <br />
            <br />
            <div style={{ display: 'flex' }}>
              <p style={{ width: '100px' }}>: </p>
              <Checkbox.Group
                options={options}
                value={checkboxValue}
                onChange={value => {
                  const checkedStatus = readCheckbox(value);
                  handleCheckboxChange(
                    checkedStatus.microscope,
                    checkedStatus.baseFloor,
                  );
                }}
              />
            </div>
          </div>
        )}
        {['doctorDayOff', 'clinicDayOff'].includes(modalMode) && (
          <div>
            <div style={{ display: 'flex' }}>
              <p style={{ width: '100px' }}>: </p>
              <div>
                <div style={{ marginBottom: '8px' }}>
                  <DatePicker
                    allowClear={false}
                    onChange={(date, dateString) => {
                      if (moment(dateString)._isValid) {
                        handleChangeDayOffStartDate(moment(dateString));
                      }
                    }}
                    value={modalArgs.dayOffDateStart}
                    style={{ marginRight: '13px' }}
                  />
                  <TimePicker
                    allowClear={false}
                    style={{ marginRight: '27px' }}
                    value={modalArgs.dayOffDateStart}
                    open={timePickerOpen2}
                    onOpenChange={handleTimePickerOpen2}
                    onChange={time => {
                      handleChangeDayOffStartTime(time);
                    }}
                    format="HH:mm"
                    addon={() => (
                      <Button
                        size="small"
                        type="primary"
                        onClick={handleTimePickerClose}
                      >
                        Ok
                      </Button>
                    )}
                    disabled={timePickerDisabled}
                  />
                  <Checkbox onChange={handleAllDayButtonChange}></Checkbox>
                </div>
                <div>
                  <DatePicker
                    allowClear={false}
                    onChange={(date, dateString) => {
                      if (moment(dateString)._isValid) {
                        handleChangeDayOffEndDate(moment(dateString));
                      }
                    }}
                    value={modalArgs.dayOffDateEnd}
                    style={{ marginRight: '13px' }}
                  />
                  <TimePicker
                    allowClear={false}
                    value={modalArgs.dayOffDateEnd}
                    open={timePickerOpen3}
                    onOpenChange={handleTimePickerOpen3}
                    onChange={time => {
                      handleChangeDayOffEndTime(time);
                    }}
                    format="HH:mm"
                    addon={() => (
                      <Button
                        size="small"
                        type="primary"
                        onClick={handleTimePickerClose}
                      >
                        Ok
                      </Button>
                    )}
                    disabled={timePickerDisabled}
                  />
                </div>
              </div>
            </div>
            {modalMode === 'doctorDayOff' && (
              <div style={{ display: 'flex', marginTop: '24px' }}>
                <p style={{ width: '100px' }}>: </p>
                <Select
                  style={{ width: 300 }}
                  placeholder=""
                  onSelect={value => {
                    handleChangeDoctor(Number(value));
                  }}
                  value={doctorValue}
                >
                  {displayDoctors}
                </Select>
              </div>
            )}
            <div style={{ display: 'flex', marginTop: '24px' }}>
              <p style={{ width: '100px' }}>: </p>
              <Select style={{ width: 300 }} defaultValue=""></Select>
            </div>
            <div style={{ display: 'flex', marginTop: '24px' }}>
              <p style={{ width: '100px' }}>: </p>
              <TextArea
                rows={4}
                onChange={e => {
                  handleNoteChange(e.target.value);
                }}
                value={doctorDayOffObject.note}
                style={{ width: '400px' }}
              />
            </div>
          </div>
        )}
      </Modal>
    );
  }
}
